import React, { useState } from "react";



export default function TextForm()
{
    const handleonchange = (event)=>
    {
    settext(event.target.value);
    return ;
    }
    const mycustomlower=()=>{
        var temp=text;
        temp=temp.toLowerCase();
        settext(temp);
        document.title="converted to lower case";
    }
const mycustomupper=()=>
    {
    var temp=text;
    temp= temp.toUpperCase();
    settext(temp);
    document.title="converted to uppercase";
    // setInterval(myTimer, 1000);
    // setInterval(mufunc,1500);
        // function mufunc()
        // {
        // document.title="sdaf";
        // }
        // function myTimer() {
        //  document.title="haha"
        // }
    }   
    const mycustomclear=()=>{
        var temp='';
        settext(temp);
    }
    const mycustomcopy=()=>{
        var temp=document.getElementById("mywords");
        navigator.clipboard.writeText(temp.value);
    }
    const [text, settext] = useState('');
    return (
        <>
        <div className="heading">
            <h1 className="mx-5"><b>Text App</b></h1>
        </div>
        <div className="body mx-5" id="item">
            <textarea value={text} rows="10" cols="100" id="mywords" onChange={handleonchange} ></textarea>
            <br></br> <br></br>
            <button className="btn btn-primary mx-2" onClick={mycustomlower}> lowercase </button>
            <button className="btn btn-primary mx-2" onClick={mycustomupper}>uppercase</button>
            <button className="btn btn-primary mx-2" onClick={mycustomcopy}>copy</button>
            <button className="btn btn-primary mx-2" onClick={mycustomclear}>clear</button>
        </div>
        <br></br>
        <div className="container" >
        <h4>text length is {text.length>0?text.length:0} || word length is {text.split(/\s+/).filter((element)=>{return element.length!=0}).length}</h4>
        </div>
        </>

    );
}